@import url("https://fonts.googleapis.com/css?family=Montserrat:600i,700i");
@import url("https://fonts.googleapis.com/css?family=Quicksand");
body {
  margin: 0;
  height: 100%;
  overflow: hidden;
  background-color: #35434d;
}

.container {
  margin: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  transition-property: all 1s;
}

.container2 {
  margin: 0;
  height: 0;
  width: 100%;
}

.iframe {
  height: 100%;
  width: 100%;
  border: none;
}

/* Left graphic layout and design */
.bg-text {
  position: absolute;
  z-index: -1;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  font-size: 2.5em;
  text-align: end;
  width: 25%;
  left: 100px;
  top: 20%;
  color: #ffc600;
}

.bot {
  padding-top: 1.2em;
  font-weight: 700;
  background: -webkit-linear-gradient(rgb(83, 84, 96), #fff);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Content box and contained elements design */
.content-box {
  display: grid;
  grid-template-rows: 2 repeat(4, 1fr);
  width: 450px;
  height: 500px;
  border-radius: 7px;
  background-color: #15232D;
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
  padding: 20px;
  font-family: "Quicksand", sans-serif;
  font-size: 1.5em;
  color: #fff;
}
.content-box a:link {
  font-family: "Quicksand", sans-serif;
  color: #ffc600;
  text-decoration: none;
  transition: all 0.5s;
}
.content-box a:hover {
  color: #fff;
}
.content-box a:visited {
  color: #ffc600;
}
.content-box a:visited:hover {
  color: #fff;
}

.title {
  display: flex;
  align-items: end;
  grid-row: 1/3;
  font-size: 2em;
  font-weight: 400;
}

.git {
  display: flex;
  align-items: center;
  grid-row: 3;
  transition: all 1s;
  width: 100px;
}

.git:hover {
  background-color: #193549;
  border-radius: 7px;
  width: 100%;
  cursor: pointer;
}

.cs {
  display: flex;
  align-items: center;
  grid-row: 4;
  transition: all 1s;
  width: 100px;
}

.cs:hover {
  background-color: #193549;
  border-radius: 7px;
  width: 100%;
  cursor: pointer;
}

.ex {
  display: flex;
  align-items: center;
  grid-row: 5;
  transition: all 1s;
  width: 200px;
}

.ex:hover {
  background-color: #193549;
  border-radius: 7px;
  width: 100%;
  cursor: pointer;
}

.li {
  display: flex;
  align-items: center;
  grid-row: 6;
  transition: all 1s;
  width: 100px;
}

.li:hover {
  background-color: #193549;
  border-radius: 7px;
  width: 100%;
  cursor: pointer;
}

/* Content Box 2 Design */
.content-box-2 {
  display: none;
  grid-template-rows: 2 repeat(4, 1fr);
  width: 450px;
  height: 500px;
  border-radius: 7px;
  background-color: rgb(242, 241, 248);
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.1);
  padding: 20px;
  font-family: "Quicksand", sans-serif;
  font-size: 1.5em;
  color: rgb(242, 241, 248);
  transition: all 1s;
  transition-property: all 1s;
}

.new-content {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  cursor: pointer;
}

.backward {
  display: flex;
  justify-content: flex-start;
  padding-left: 20px;
  grid-column: 1;
}

.forward {
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  grid-column: 3;
}

#return {
  display: flex;
  justify-content: center;
  cursor: pointer;
}

@media (max-width: 1242px) {
  .bg-text {
    color: rgb(247, 247, 249);
  }
  .bot {
    background: rgb(247, 247, 249);
  }
}
@media (max-width: 500px) {
  .content-box {
    width: 100%;
    height: 100VH;
  }
}